.cancelMask {
    position: absolute 0 * *;
    height: 100%;
    z-index: 4;
    background-color: var(--color-black-alpha);
    ::-webkit-scrollbar {
        display: none; 
    }

    &__wrapper {
        position: absolute;
        size: 100% 100%;

        &--write {
            height: 66.7%;
        }
        &--read {
            height: 83%;
        }
        &--notification {
            height: 93%;
        }
    }
}

.profile {
    &__avatar {
        left: calc(50% - 24px);
    }
    &__locked {
        text-align: center;

        svg {
            size: 40px;
        }
    }
    &__count {
        position: relative -17px * *;
        height: 50px;

        &__column {
            position: relative;
            lost-column: 1/3;
            text-align: center;

            &::before {
                content: attr(data-count);
                position: absolute * * -17px 0;
                width: 100%;
                font-size: 11px;
                color: var(--color-steel);
            }

            svg {
                size: 27px;
            }
        }
    }
    &__createdAt {
        position: absolute 3px 0 0 3px;
    }
    &__following {
        position: absolute 5px * * 5px;
        size: 17px;
    }
    &__biography {
        position: relative -17px * *;
        padding: 3px 7px;
        white-space: pre-line;
        font-size: 13px;
        color: var(--color-gray);
    }
    &__website {
        text-align: center;

        svg {
            size: 17px;
            stroke-width: 7px;
        }
    }
    &--twitter {
        ::selection {
            color: rgba(42, 169, 224, 0.7);
            background-color: var(--color-gray);
        }

        .profile__biography {
            background-color: var(--color-twitter);

            a, a:visited {
                color: #78D6FF;
            }
        }
        .profile__website {
            svg {
                stroke: var(--color-twitter);
            }   
        }
    }
    &--instagram {
        ::selection {
            color: rgba(63, 93, 135, 0.7);
            background-color: var(--color-gray);
        }

        .profile__biography {
            background-color: var(--color-instagram);

            a, a:visited {
                color: #82B6FF;
            }
        }
        .profile__website {
            svg {
                stroke: var(--color-instagram);
            }
        }
    }
    &--weibo {
        ::selection {
            color: rgba(230, 22, 45, 0.7);
            background-color: var(--color-gray);
        }

        .profile__biography {
            background-color: var(--color-weibo);

            a, a:visited {
                color: #FF7A88;
            }
        }
        .profile__website {
            svg {
                stroke: var(--color-weibo);
            }
        }
    }
}

.mask {
    padding: 0 0 7px;
    margin: 36px 7px 0 7px;

    &__menu {
        &--row {
            position: absolute;
            left: calc(50% - 35px);

            &--1 {
                top: calc(50% - 35px);
            }
            &--2 {
                &--1 {
                    top: calc(33.3% - 35px);
                }
                &--2 {
                    top: calc(66.7% - 35px);
                }
            }
            &--3 {
                &--1 {
                    top: calc(25% - 35px);
                }
                &--2 {
                    top: calc(50% - 35px);
                }
                &--3 {
                    top: calc(75% - 35px);
                }
            }
        }
        &__button {
            size: 70px;

            &--switch {
                position: absolute;
                top: calc(50% - 35px);

                &--left {
                    left: 0;
                    transform: rotate(180deg);
                }
                &--right {
                    right: 0;
                }
            }
        }
    }
    &__user_timeline {
        padding: 10px 10px 0px 10px;
        margin: 17px;
        background-color: var(--color-gray);

        .timeline__content {
            top: -12px;
        }
        .timeline--quote {
            background-color: var(--color-gray-drak);
        }
        .actions__count::after {
            color: var(--color-steel);
        }

        &--gutter {
            margin: 36px 17px 17px;
            padding: 0 10px;
            background-color: var(--color-gray);
        }

        &--location {
            .timeline__location {
                transform: translateY(-10px);
            }
        }
    }
    &__timeline {
        margin: 36px 17px 17px;
        background-color: var(--color-gray);

        .timeline--quote {
            background-color: var(--color-gray-drak);
        }
    }
    &__notification {
        position: relative 70px * *;
        margin: 0 7px 0;
        padding: 17px 3px 7px 3px;
        height: calc(93vh - 140px);

        &__source {
            position: absolute 3px 3px * *;
            text-align: center;
            user-select: none;
            z-index: 2;

            svg {
                width: 1.4em;
            }
        }
    }
    &__header {
        padding: 17px;
        text-align: center;

        &__title {

            > * {
                display: inline-block;
                height: 37px;
                vertical-align: middle;
            }

            span {
                font-size: var(--font-size-regular) 37px;
                font-weight: var(--font-weight-light);
            }
        }
        &--twitter {
            color: var(--color-twitter);
        }
        &--instagram {
            color: var(--color-instagram);
        }
        &--weibo {
            color: var(--color-weibo);
        }
    }
    &--empty {
        position: absolute calc(50% - 50px) * * calc(50% - 50px);
        size: 100px;
    }
}

.direct {

    &__container {
        position: absolute 17px * * 0px;
        size: 100% calc(100% - 34px);
    }
    &__sender {
        position: absolute -17px * * 5px;
        width: 96px;
        max-height: calc(93vh - 140px);
        z-index: 3;
        &::-webkit-scrollbar { 
            display: none; 
        }

        button {
            display: block;
            position: relative;
            margin-bottom: 3px;
            size: 32px;

            img {
                width: 100%;
                border-radius: 50%;
            }

            &:hover {
                .direct__sender__name {
                    opacity: 1;
                }
            }
        }
        &__name {
            display: inline-block;
            position: absolute 9px * * 37px;
            padding: 1px;
            width: 59px;
            font-size: var(--font-size-xx-small) 14px;
            color: var(--color-white);
            background-color: var(--color-twitter);
            border-radius: 3px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            opacity: 0;
        }
    }
    &__msg {
        position: relative;
        width: 100%;
        margin: 7px 0;
        padding: 7px;

        &--me {
            float: right;

            ::selection {
                color: rgba(42, 169, 224, 0.7);
                background-color: var(--color-gray);
            }
            .direct__msg__content {
                float: right;
                color: var(--color-gray);
                background-color: var(--color-twitter);
            }
            .direct__msg__time {
                right: 7px;
            }
        }
        &--others {
            float: left;

            .direct__msg__content {
                float: left;
                color: var(--color-black);
                background-color: var(--color-gray);
            }
            .direct__msg__time {
                left: 7px;
            }
        }

        &__content {
            display: inline-block;
            max-width: 66.7%;
            padding: 3px 7px;
            border-radius: 3px;

            a {
                text-decoration: underline;
            }
        }
        &__text {
            word-wrap: break-word;
            white-space: pre-wrap;
            line-height: 1.4;
            cursor: text;
        }
        &__time {
            position: absolute;
            bottom: -3px;
            color: #89a;
            font-size: var(--font-size-xx-small);
            cursor: pointer;
        }
    }
    &__write {
        position: absolute calc(100% - 7px) * * 0;
        size: 100% 32px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border-bottom-left-radius: 3px;
        border-bottom-right-radius: 3px;
        border-top: 1px solid var(--color-gray-drak);
        background-color: var(--color-white);
    }
}
